es/audit/welfareFund/src/pages/audit/welfareFund/unaudited.vue"],"names":[],"mappings":";AAwNA;EACA,aAAA;EACA,iBAAA;EACA,wBAAA;EACA,qBAAA;EACA,6BAAA;EACA,sBAAA;CACA;AACA;EACA,kBAAA;EACA,kCAAA;CACA;AAEA;EACA,cAAA;EACA,gBAAA;CACA;AAEA;EACA,aAAA;EACA,iBAAA;EACA,kBAAA;CACA;AAEA;EACA,YAAA;EACA,eAAA;EACA,UAAA;EACA,YAAA;CACA;AAEA;EACA,mBAAA;CACA;AAEA;EACA,4BAAA;EACA,cAAA;CACA;AAEA;EACA,sBAAA;EACA,WAAA;CACA;AAEA;EACA,sBAAA;EACA,WAAA;EACA,cAAA;EACA,aAAA;EACA,8BAAA;CACA;AAEA;EACA,aAAA;EACA,aAAA;EACA,mBAAA;EACA,mBAAA;EACA,oBAAA;EACA,uDAAA;UAAA,+CAAA;EACA,aAAA;EACA,YAAA;EACA,UAAA;EACA,UAAA;CACA;AAEA;EACA,iCAAA;EACA,kBAAA;CACA;AAEA;EACA,0BAAA;CACA;AAEA;EACA,YAAA;EACA,WAAA;CACA;AAEA;EACA,aAAA;EACA,WAAA;CACA;AACA;EACA,wBAAA;EACA,mBAAA;EACA,oBAAA,CAAA,YAAA;EACA,wBAAA,CAAA,qBAAA;EACA,iBAAA;EACA,eAAA;CACA;AACA;EACA,gBAAA;CACA;AACA;EACA,uBAAA;EACA,2BAAA;CACA","file":"unaudited.vue","sourcesContent":["<template>\n  <div>\n    <ctbHead>\n      <router-link :class=\"seCurId==item.id? 'cur':''\"\n                   v-for=\"item in authList\"\n                   :key=\"item.id\"\n                   :to=\"{path:item.action,query:{id:curId}}\"\n                   :v-key=\"item.id\">\n        {{item.name}}\n      </router-link>\n    </ctbHead>\n    <div class=\"container\">\n      <div class=\"quote\">\n        <div class=\"quote-ele\"><i></i>审核-公益金管理-未审核</div>\n        <div class=\"quote-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <el-table :data=\"list\">\n        <el-table-column prop=\"name\"\n                         label=\"姓名\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"age\"\n                         label=\"年龄\"\n                         align=\"center\"></el-table-column>\n\n        <el-table-column label=\"婚否\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            {{scope.row.marriage==0 ? '未婚' : '已婚'}}\n          </template>\n        </el-table-column>\n        <el-table-column prop=\"politics\"\n                         label=\"政治面貌\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"career\"\n                         label=\"职业\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"arms\"\n                         label=\"服役单位\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"start_time\"\n                         label=\"入伍时间\"\n                         align=\"center\"></el-table-column>\n        <el-table-column prop=\"end_time\"\n                         label=\"转业时间\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"地址\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-popover placement=\"top-start\"\n                        title=\"地址详情\"\n                        width=\"200\"\n                        trigger=\"hover\"\n                        :content=\"`${scope.row.province}${scope.row.city}${scope.row.county}${scope.row.address}`\">\n              <el-button type=\"text\"\n                         slot=\"reference\"\n                         class=\"ellipsis\">{{`${scope.row.province}${scope.row.city}${scope.row.county}${scope.row.address}`}}</el-button>\n            </el-popover>\n          </template>\n        </el-table-column>\n        <el-table-column prop=\"create_time\"\n                         label=\"申请时间\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"through(scope.row)\">通过</el-button>\n            <el-button type=\"danger\"\n                       size=\"small\"\n                       @click=\"rejected(scope.row)\">驳回</el-button>\n          </template>\n\n        </el-table-column>\n      </el-table>\n      <!-- 分页 -->\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list && list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\n\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      page: 0,\n      pageCount: 0,\n      authList: [],\n      threeAuthList: []\n    }\n  },\n  created () {\n    this.init();\n  },\n\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/UserWelfare/welfareApply', { token: this.token, page: this.page, status: 0 })\n        .then(res => {\n\n          this.list = res.data.data.list;\n\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n    //通过\n    through (row) {\n      this.$confirm('此操作将通过审核, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning',\n        center: true\n      }).then(async () => {\n        try {\n          const res = await this.$axios.post('admin/UserWelfare/welfareAdopt', { token: this.token, id: row.id })\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: \"success\" })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n\n        } catch (err) {\n          throw (err)\n        }\n      }).catch(() => { });\n    },\n    //驳回\n    rejected (row) {\n      this.$prompt('请输入驳回理由', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        inputPattern: /\\S/,\n        inputErrorMessage: '驳回理由不能为空'\n      }).then(async ({ value }) => {\n        try {\n          const res = await this.$axios.post('admin/UserWelfare/welfareReject', { token: this.token, id: row.id, reason: value })\n          if (res.data.code == 1) {\n            this.$message({ message: res.data.msg, type: 'success' })\n            this.init()\n          } else {\n            this.$message.error(res.data.msg)\n          }\n        } catch (error) {\n          throw (error)\n        }\n      }).catch(() => {\n        this.$message({\n          type: 'info',\n          message: '取消输入'\n        });\n      });\n    }\n\n\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '公益金管理') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '未审核' && arr[i].name == '公益金管理') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n}\n</script>\n<style scoped>\n.ellipsis {\n  width: 150px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n}\n.laybox2 ul {\n  margin: 33px auto;\n  border-bottom: 1px dashed #04bbfc;\n}\n\n.laybox2 {\n  padding: 60px;\n  font-size: 16px;\n}\n\n.laybox2 li {\n  width: 300px;\n  text-align: left;\n  margin: 33px auto;\n}\n\n.t-c:after {\n  content: \"\";\n  display: block;\n  height: 0;\n  clear: both;\n}\n\n.bot > div {\n  position: relative;\n}\n\ndiv.bot {\n  margin: 45px auto 35px auto;\n  height: 100px;\n}\n\ndiv.bot span {\n  display: inline-block;\n  width: 20%;\n}\n\ndiv.bot input {\n  display: inline-block;\n  width: 55%;\n  outline: none;\n  border: none;\n  border-bottom: 1px solid #aaa;\n}\n\ndiv.bot button {\n  width: 110px;\n  height: 36px;\n  margin-left: -55px;\n  position: absolute;\n  border-radius: 18px;\n  box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n  border: none;\n  color: #fff;\n  left: 50%;\n  top: 60px;\n}\n\nbutton.reject {\n  background: rgba(237, 81, 55, 1);\n  margin-right: 30%;\n}\n\nbutton.pass {\n  background-color: #20a0ff;\n}\n\n.left {\n  float: left;\n  width: 50%;\n}\n\n.right {\n  float: right;\n  width: 50%;\n}\n.nowrap {\n  width: 120px !important;\n  text-align: center;\n  white-space: nowrap; /*规定文本不换行**/\n  text-overflow: ellipsis; /**显示省略符号来代表被修剪的文本。*/\n  overflow: hidden;\n  margin: 0 auto;\n}\n.regionDetail >>> .el-dialog__title {\n  font-size: 22px;\n}\n.regionDetail >>> .el-dialog__body {\n  padding: 0px 25px 30px;\n  border-top: solid 2px #eee;\n}\n</style>\n"],"sourceRoot":""}]);

